<template>
  <div>
    <p>checklist page for customer {{accountId}}</p>
    <div>
      <div class="checllist_item" v-for="checkName in checkNames">
        <span v-if="checks[checkName]"> {{checks[checkName].description}} <span v-bind:class="checks[checkName].flag"> {{flagText(checks[checkName].flag)}}</span></span>
        <span v-else>Loaing check..</span>
      </div>
      <div class='sheet_link'>
        <span v-if="sheetUrl"> <a class="sheet_button" v-bind:href="sheetUrl">Click to get sheet results</a></span>
        <span v-else>Loading sheet link..</span>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  props: ['customerId'],
  data () {
    return {
      displayResponse: null,
      accountId: this.customerId,
      checkNames: [],
      checks: {
        'conversions_check': null,
        'broad_modifiers_check': null,
        'mobile_firendly_pages': null,
        'low_quality_keywords': null,
        'has_negatives': null,
        'has_changes':null,
        'has_more3_ads': null,
        'search_ctr': null,
        'ave_position': null,
        'have_trials': null
      },
      sheetUrl: null
    }
  },
  methods: {
    getCheckFlag (checkName) {
      const path = '/api/check_account/' + this.customerId + '/' + checkName
      axios.get(path)
      .then(response => {
        console.log(response.data);
        this.checks[checkName] = response.data
      })
      .catch(error => {
        console.log(error)
        // this.$router.push('/')
      })
    },
    getSheetUrl() {
      const path = '/api/create_sheet/' + this.customerId
      axios.get(path)
      .then(response => {
        console.log(response.data);
        this.sheetUrl = response.data.url
      })
      .catch(error => {
        console.log(error)
        // this.$router.push('/')
      })
    },
    flagText(text) {
      switch (text) {
        case 'red':
          return 'Test Failed'
        case 'green':
          return 'Test Passed'
        case 'amber':
          return 'Test Passed Partly'
        default:
          return 'No data'
      }
    }
  },
  created () {
    //
    for (let checkName in this.checks) {
      this.checkNames.push(checkName)
      this.getCheckFlag(checkName)
    }
    this.getSheetUrl()
  }
}
</script>
<style media="screen">
  .red {
    color: red
  }

  .amber {
    color: darkorange
  }
  .green {
    color: green
  }
</style>



